import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { FormRowUtil, IFormList, FormActionMap, FormEvent } from '../light-common/index';
import { LightWriterConfig } from "./light-writer.config";

@Component({
    selector: 'light-writer',
    templateUrl: 'light-writer.component.html',
    // styleUrls: ['light-writer.component.scss']
})
export class LightWriterComponent implements OnInit {

    //读取数据的信息
    @Input() writerData: any = {};

    //表单的列表对象信息
    formListArray: Array<IFormList> = [];

    //表单动作映射
    formActionMap: FormActionMap = null;

    //表单事件信息
    formEvent: FormEvent = null;

    //表单的配置信息
    formConfig: LightWriterConfig = new LightWriterConfig();

    //读取数据的信息
    @Input() headFlag: boolean = true;

    /**
     * 写入的组织控件的信息
     * @memberof LightWriterComponent
     */
    constructor() {

    }

    /**
     * 内部的初始化信息
     * @memberof LightReadComponent
     */
    public ngOnInit(): void {
        this.formListArray = [];
        this.formActionMap = new FormActionMap();
        this.formEvent = new FormEvent();
        this.formConfig.headFlag = this.headFlag;
        this._flush();
    }

    //改变的状态信息
    ngOnChanges(changes: SimpleChanges): void {
        this.ngOnInit();
    }

    /**
     * 内部的数据刷新操作
     * @private
     * @memberof LightReadComponent
     */
    private _flush(): void {
        if (!!this.writerData) {
            this.formListArray = FormRowUtil.getFormDta(this.writerData, this.formEvent);
        }
    }

}



