import { NgModule } from '@angular/core';

import { IonicModule } from 'ionic-angular';
import { LightProvidersModule } from '../light-providers/light-providers.module';
import { LightUtilModule } from '../light-util/light-util.module';
import { LightNationModule } from '../light-nation/light-nation.module';

import { LightContentComponent, LightListComponent, LightDateComponent } from './light-components.index';
import { MemberImageComponent, ContactContentComponent, ContactMenuComponent, LightContactComponent, LightMemberDetailComponent, ContactComponent } from './light-contact/index';
import { LightReadComponent, LightReadDivComponent, LightReadGroupComponent, LightReadTextComponent, LightReadTextNormalComponent, LightReadTextPhoneComponent, LightReadImageComponent, LightReadService, LightReadTableComponent, LightReadRowComponent } from './light-read/index';
import { LightWriterTextComponent, LightWriterTextareaComponent, LightWriterComponent, LightWriterDataComponent, LightWriterDateComponent, LightWriterUserComponent, LightWriterDivComponent, LightWriterGroupComponent, LightWriterImageComponent, LightWriterTableComponent, LightWriterRowComponent, LightWriterService } from './light-writer/index';
import { WriterTextComponent, WriterNumberComponent, WriterToggleComponent, WriterCheckComponent, WriterMutiComponent } from './light-writer/index';
import { LightFlowComponent, LightFlowHandleComponent, LightFlowBackComponent, LightFlowDeliverComponent, LightFlowService, HandleMutiContactComponent, HandleMutiPeopleComponent, HandleSingleContactComponent, HandleSinglePeopleComponent, HandleStepComponent, BackShowStepComponent, BackSubmitStepComponent } from './light-flow/index';
import { LightAttachComponent } from './light-attach/index';


///导出的列表信息
var dataList = [
    //公共基础组件
    LightContentComponent, LightListComponent, LightDateComponent,
    //通讯录组件
    MemberImageComponent, ContactContentComponent, ContactMenuComponent, LightContactComponent, LightMemberDetailComponent, ContactComponent,
    //读取表单组件
    LightReadComponent, LightReadDivComponent, LightReadGroupComponent, LightReadTextComponent, LightReadTextNormalComponent, LightReadTextPhoneComponent, LightReadImageComponent, LightReadTableComponent, LightReadRowComponent,
    //写入表单组件
    LightWriterTextComponent, LightWriterTextareaComponent, LightWriterComponent, LightWriterDataComponent, LightWriterDateComponent, LightWriterUserComponent, LightWriterDivComponent, LightWriterGroupComponent, LightWriterImageComponent, LightWriterTableComponent, LightWriterRowComponent,
    //写入表单组件
    WriterTextComponent, WriterNumberComponent, WriterToggleComponent, WriterCheckComponent, WriterMutiComponent,
    //流程表单组件
    LightFlowComponent, LightFlowHandleComponent, LightFlowBackComponent, LightFlowDeliverComponent, HandleMutiContactComponent, HandleMutiPeopleComponent, HandleSingleContactComponent, HandleSinglePeopleComponent, HandleStepComponent, BackShowStepComponent, BackSubmitStepComponent,
    //附件的组件信息
    LightAttachComponent,
];

var providerList = [
    LightReadService,
    LightWriterService,
    LightFlowService
];

@NgModule({
    imports: [
        IonicModule,
        LightUtilModule,
        LightProvidersModule,
        LightNationModule
    ],
    declarations: dataList,
    exports: dataList,
    providers: providerList,
    entryComponents: [
        //公共基础组件
        LightContactComponent, LightMemberDetailComponent
    ]
})
export class LightComponentsModule { }
export * from './light-components.index';




