///字符串的工具类
export class LightStringRegexUtil {

    /**
     * 检查是否为手机号码
     * 验证规则：11位数字，以1开头。
     * @static
     * @param {string} 手机的文本信息 
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkMobileNumber(mobileText: string): boolean {
        var result = false;
        var re = /^1\d{10}$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

    /**
     * 验证电话号码
     * 验证规则：区号+号码，区号以0开头，3位或4位
     * 号码由7位或8位数字组成
     * 区号与号码之间可以无连接符，也可以“-”连接
     * 如01088888888,010-88888888,0955-7777777
     * @static
     * @param {string} mobileText 
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkPhone(mobileText: string): boolean {
        var result = false;
        var re = /^0\d{2,3}-?\d{7,8}$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

    /**
     * 验证邮件的操作
     * 
     * @static
     * @param {string} mobileText 
     * @returns {boolean} 
     * 
     * @memberof LightStringRegexUtil
     */
    public static checkEmail(mobileText: string): boolean {
        var result = false;
        var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
        if (!!mobileText && re.test(mobileText)) {
            result = true;
        }
        return result;
    }

}


