///用户的数字工具类
export class LightNumberUtil{

   /**
    * 转化为文本百分比
    * @static
    * @param {number} value 
    * @returns {string} 
    * 
    * @memberof LightNumberUtil
    */
   public static toPercentText(value:number):string{
         return value.toFixed(2) + '%';
   }

   /**
    * 判断是否为数字信息
    * @param target 
    */
   public static isNumber(target:any):boolean{
         var result=false;
         if(!!target&&typeof target === 'number'){
             result=true;
         }
         return result;
   }

}


