import { Injectable } from '@angular/core';
import { LightNation } from '../common/lightNation';
import { ImagePicker, ImagePickerOptions } from '@ionic-native/image-picker';

@Injectable()
export class LightImagePicker extends LightNation {

    /**
     * 参数对象 
     *
     * @private
     * @type {ImagePickerOptions}
     * @memberof LightImagePicker
     */
    private _options: ImagePickerOptions;

    /**
     * ionic cordova plugin add cordova-plugin-telerik-imagepicker --variable PHOTO_LIBRARY_USAGE_DESCRIPTION="your usage message"
     * $ npm install --save @ionic-native/image-picker
     * 加载ionic插件:cordova plugin add cordova-plugin-image-picker
     * @param {ImagePicker} imagePicker 
     * @memberof LightImagePicker
     */
    constructor(private imagePicker: ImagePicker) {
        super();
        this._options = {
            maximumImagesCount: 6,
            //width: IMAGE_SIZE,
            //height: IMAGE_SIZE,
            //quality: QUALITY_SIZE
        }
    }

    getPicture(resultDefault: any = null): Promise<any> {
        if (!!!resultDefault) {
            resultDefault = "https://www.baidu.com/img/bd_logo1.png"
        }
        var result: Promise<any> = this._getDefaultResult(resultDefault);
        if (!this._isWeb()) {
            result = new Promise((resolve, reject) => {
                this.imagePicker.getPictures(this._options).then((results) => {
                    resolve(results);  //返回选中多张图片数组
                }).catch(errText => {
                    reject(errText);
                })
            })
        }
        return result;
    }

}


