import { Component, ViewChild } from '@angular/core';
import { Platform, NavController, IonicApp } from 'ionic-angular';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage, LoginPage } from '../pages/common/index';

import { LightConfig, LightUser, LightJPush, LightHotCodePush, LightProviders } from '../light/light-out/light-out.module';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {

  @ViewChild('myNav') nav: NavController;

  rootPage: any = null;

  constructor(
    platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    public lightConfig: LightConfig,
    public lightJPush: LightJPush,
    public lightHotCodePush: LightHotCodePush,
    public lightProviders: LightProviders,
    public lightUser: LightUser,
    private ionicApp: IonicApp,
  ) {
    platform.ready().then(() => {

      statusBar.styleDefault();

      splashScreen.hide();

      this._init();

      this._cacheInit();

      this.lightJPush.initJpush(this.nav, 'LightMessageComponent', this.lightProviders);

      // this.lightHotCodePush.initCodePush();

    });
  }

  /**
   * 项目初始化配置信息
   * @private
   * @memberof MyApp
   */
  private _init(): void {

    this.lightConfig.baseUrl = "http://61.136.243.46:8092/"; //公司外网IP     
    // this.lightConfig.baseUrl = "./";

    this.lightConfig.timeOut = 2000;

    this.lightUser.autoLoginCheck().then(() => {
      this.rootPage = TabsPage;
    }).catch(() => {
      this.rootPage = LoginPage;
    });

    this.lightUser.registerBackButtonAction(this.ionicApp, this.nav);

  }


  /**
   * 项目缓存的配置信息
   * @private
   * @memberof MyApp
   */
  private _cacheInit(): void {

  }

}



