///用户对象信息
export class UserObject {

    //数据的对象
    data: object = null;

    //组织的ID信息
    organizeID: string = null;

    //组织机构的名称
    organizeName: string = null;

    constructor(
        data: object,
        organizeName?: string,
        organizeID?: string
    ) {
        this.data = data;
        this.organizeName = organizeName;
        this.organizeID = organizeID;
    }

    //组织机构名称
    get OrganizeName(): string {
        return this.organizeName;
    }

    //获取ID的对象
    get ID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["ID"];
        }
        return result;
    }

    //获取父节点的ID对象
    get PID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["PID"];
        }
        return result;
    }

    //名称对象信息
    get Name(): string {
        var result: string = "";
        if (!!this.data) {
            result = this.data["Name"];
        }
        return result;
    }

    //职位对象信息
    get Title(): string {
        var result: string = "";
        if (!!this.data) {
            result = this.data["Title"];
        }
        return result;
    }

    //扩展的自定义对象
    get Extend(): object {
        var result: object = null;
        if (!!this.data) {
            result = this.data["Extend"];
        }
        return result;
    }

}


