import { Injectable } from '@angular/core';
import { Platform, ToastController, NavController, Events } from 'ionic-angular';
import { JPush } from '@jiguang-ionic/jpush';
import { LightNation } from '../common/lightNation';
import { LightProviders } from '../../light-providers/light-providers.module';

@Injectable()
export class LightJPush extends LightNation {

    /**
     * 加载nation插件命令: npm install --save @jiguang-ionic/jpush 
     * 加载ionic插件: ionic cordova plugin add jpush-phonegap-plugin --variable APP_KEY=your_jpush_appkey
     * Creates an instance of LightJPush.
     * @param {JPush} jPush 
     * @memberof LightJPush
     */

    constructor(
        public platform: Platform,
        public jPush: JPush,
        public toastCtrl: ToastController
    ) {
        super();
    }

    //添加本地推送的方法
    addLocalNotification() {
        if (!this.platform.is('ios')) {
            this.jPush.addLocalNotification(0, "Hello jPush", "jPush", 1, 5000);
        } else {
            this.jPush.addLocalNotificationForIOS(5, "Hello jPush", 1, "localNoti1");
        }
    }

    //推送初始化
    public initJpush(navCtrl: NavController, messagePage: any, lightProviders: LightProviders) {
        if (!this._isWeb()) {
            // window['plugins'].jPushPlugin.init();
            // if (this.platform.is('ios')) {
            //     window['plugins'].jPushPlugin.setDebugModeFromIos();
            //     window['plugins'].jPushPlugin.setApplicationIconBadgeNumber(0);
            // } else {
            //     window['plugins'].jPushPlugin.setDebugMode(true);
            //     window['plugins'].jPushPlugin.setStatisticsOpen(true);
            // }
            this.jPush.init();

            this.getRegistrationID(lightProviders);

            this.jPushAddEventListener(navCtrl, messagePage);
        }
    }

    private getRegistrationID(lightProviders: LightProviders) {
        this.jPush.getRegistrationID().then(regId => {
            if (regId == '') {
                console.log('重新获取');
                setTimeout(() => {
                    this.getRegistrationID(lightProviders);
                }, 1000)
            } else {
                console.log(regId);
                lightProviders.lightLocalCache.set('registrationID', regId);
            }
        })
    };

    private jPushAddEventListener(navCtrl: NavController, messagePage: any) {
        //判断系统设置中是否允许当前应用推送
        this.jPush.getUserNotificationSettings().then(result => {
            if (result == 0) {
                console.log('系统设置中已关闭应用推送');
            } else if (result > 0) {
                console.log('系统设置中打开了应用推送');
            }
        });

        //点击通知进入应用程序时会触发的事件
        document.addEventListener("jpush.openNotification", event => {

            let content = this.platform.is('ios') ? event['aps'].alert : event['alert'];

            this.toPages(navCtrl, messagePage);

            console.log("jpush.openNotification" + content);
        }, false);

        //收到通知时会触发该事件
        document.addEventListener("jpush.receiveNotification", event => {
            let content = this.platform.is('ios') ? event['aps'].alert : event['alert'];

            const toast = this.toastCtrl.create({
                message: content,
                duration: 1500,
                position: 'top'
            });

            toast.onDidDismiss(() => {
                console.log('Dismissed toast');
                // if(){

                // }
            });

            setTimeout(() => {

            }, 1400);

            toast.present();

            console.log("jpush.receiveNotification" + content);
        }, false);

        //收到自定义消息时触发这个事件
        document.addEventListener("jpush.receiveMessage", event => {
            let message = this.platform.is('ios') ? event['content'] : event['message'];
            console.log("jpush.receiveMessage" + message);
        }, false);

    }

    //设置别名,一个用户只有一个别名
    public addAlias(jPushData: any, lightProviders: LightProviders) {
        if (!this._isWeb()) {
            lightProviders.lightHttp.post(jPushData.url, {
                registrationId: jPushData.registrationId,
                alias: jPushData.alias
            })
        }
    }

    //删除别名,一个用户只有一个别名
    public delAlias(jPushData: any, lightProviders: LightProviders) {
        if (!this._isWeb()) {
            lightProviders.lightHttp.post(jPushData.url, {
                alias: jPushData.alias
            }).then(res => {
                console.log('删除别名成功！');
            })
        }
    }

    // 极光推送
    jPushOpenNotification(events: Events) {
        // 当点击极光推送消息跳转到指定页面
        events.subscribe('jpush.openNotification', content => {
            // const childNav = this.nav.getActiveChildNav();
            // if (childNav) {
            //     const tab = childNav.getSelected();
            //     const activeVC = tab.getActive();
            //     // if (activeVC.component == AboutPage) {//如果当前所在页面就是将要跳转到的页面则不处理
            //     //   return;
            //     // }
            //     const activeNav = activeVC.getNav();
            //     activeNav.popToRoot({}).then(() => { // 导航跳到最顶层
            //         childNav.select(3); // 选中第四个tab
            //         const t = childNav.getSelected(); // 获取选中的tab
            //         const v = t.getActive(); // 通过当前选中的tab获取ViewController
            //         const n = v.getNav(); // 通过当前视图的ViewController获取的NavController
            //         n.push(AboutPage); // 跳转到指定页面
            //     });
            // }
        });
    }

    //跳转消息页面
    private toPages(navCtrl: NavController, messagePage: any) {
        const childNav = navCtrl.getActiveChildNav();
        if (childNav) {
            const tab = childNav.getSelected();
            const activeVC = tab.getActive();
            // if (activeVC.component == messagePage) {//如果当前所在页面就是将要跳转到的页面则不处理
            //   return;
            // }
            const activeNav = activeVC.getNav();
            activeNav.popToRoot({}).then(() => { // 导航跳到最顶层
                childNav.select(3); // 选中第四个tab
                const t = childNav.getSelected(); // 获取选中的tab
                const v = t.getActive(); // 通过当前选中的tab获取ViewController
                const n = v.getNav(); // 通过当前视图的ViewController获取的NavController
                n.push(messagePage); // 跳转到指定页面
            });
        }
    }

}


