import { LoadingController, Loading } from 'ionic-angular';
import { Injectable } from '@angular/core';

@Injectable()
export class LightLoad {

    //自动关闭的时间
    private autoTimeClose: number = 100000;

    //加载的方法信息
    constructor(
        public loadingCtrl: LoadingController
    ) {

    }

    /**
     * 展现加载的操作
     * 关闭加载的页面选用loader.dismiss()的方法
     * @param {string} 文本内容 
     * 
     * @memberof LightLoad
     */
    public showLoading(content: string = "正在加载中..."): Loading {
        var loading = this.loadingCtrl.create({ spinner: 'circles', content: content, duration: this.autoTimeClose });
        loading.present();
        return loading;
    }

}


