@NgModule({
    imports: [
        HttpModule,
        // IonicModule,
        LightUtilModule
    ],
    declarations: [],
    providers: [
        LightHttp, LightConfig, LightUserConfig,
        LightLocalCache, LightPlatform, LightPop,
        LightLoading, LightLoad, LightUser,
        LightMemberImage, LightExit, LightMethods,
        LightNav, LightProviders, LightModal
    ],
    exports: []
})
export class LightProvidersModule { }
export * from './providers/index';


