/*
 *此模块用于操作所需要的light模块集合调用
 */

import { NgModule } from '@angular/core';
// import { IonicModule } from 'ionic-angular';

//基础模块
import { LightComponentsModule } from '../light-components/light-components.module';
import { LightContentComponent, LightListComponent, LightWriterComponent, LightReadComponent, LightAttachComponent } from '../light-components/light-components.module';
//以下按需加载
var lightComponentsList = [
    //公共基础组件
    LightContentComponent, LightListComponent,
    //读取写入表单组件
    LightReadComponent, LightWriterComponent,
    //附件的组件信息
    LightAttachComponent
];

//地图模块
//基础地图控件的导出
// import { TileLayerComponent, TileLayerWmsComponent, TileLayerWmtsComponent, ArcgisLayerDynamicComponent } from '../light-map/light-map.module';
//地图控件的导出
// import { LightPageComponent, LightMapModule, LightPageUrlComponent, LightMapComponent, LightMapUrlComponent, LightPageArcgisComponent } from '../light-map/light-map.module';
//地图矢量图层组件的导出
// import { GroupLayerComponent, GraphicsLayerComponent, GeoPointComponent, GeoLineComponent, GeoPolygonComponent } from '../light-map/light-map.module';
//地图矢量图层组件的导出
// import { LegendPointIconComponent, LegendPointCircleComponent, LegendLineComponent, LegendLineDottedComponent, LegendLineSolidComponent } from '../light-map/light-map.module';
//以下按需加载
var lightMapList = [
    // LightPageComponent, LightPageUrlComponent, LightPageArcgisComponent
];

//基础配置模块
import { LightProvidersModule } from '../light-providers/light-providers.module';

//数据处理转换模块
import { LightUtilModule } from '../light-util/light-util.module';

var importsList = [
    // IonicModule,
    LightComponentsModule,
    // LightMapModule,
    LightProvidersModule,
    LightUtilModule
];

///数据的字节数组
var exportsList = new Array();

var entryComponentsList = new Array();

@NgModule({
    imports: importsList,
    declarations: [],
    exports: exportsList.concat([], lightComponentsList),
    entryComponents: entryComponentsList.concat([], lightMapList)
})
export class LightOutModule { }
export * from '../light-util/light-util.index';
export * from '../light-providers/light-providers.index';
export * from '../light-components/light-components.index';
export * from '../light-nation/light-nation.index';
// export * from '../light-map/light-map.index';




