import { Component, OnInit, Input } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { LightHttp, LightPop } from '../../light-providers/light-providers.module';
import { LightJsonUtil, LightStringUtil } from '../../light-util/light-util.module';
import { LightFile, LightFileTransfer, LightFileOpen } from '../../light-nation/light-nation.module';

@Component({
  selector: 'light-attach',
  templateUrl: 'light-attach.component.html'
})
export class LightAttachComponent implements OnInit {

  /**
   * 附件列表
   * @type {Array<any>}
   * @memberof LightAttachComponent
   */
  attachList: Array<any> = null;

  /**
   * 设置的对应的编码ID
   * @type {string}
   * @memberof LightAttachComponent
   */
  @Input() code: string = null;

  /**
   * 附件的操作
   * @memberof LightAttachComponent
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public lightHttp: LightHttp,
    public lightPop: LightPop,
    public lightFileOpen: LightFileOpen,
    public lightFileTransfer: LightFileTransfer,
    public lightFile: LightFile
  ) {

  }

  /**
   * 
   * 内部初始化
   */
  public ngOnInit(): void {
    if (!!this.code) {
      //附件
      this.lightHttp.get("Attachment/GetAttachments", { objectID: this.code }).then((data) => {
        this.attachList = LightJsonUtil.toObject(data) as Array<any>;
        for (let i = 0; i < this.attachList.length; i++) {
          this.attachList[i].Status = '下载';
          this.lightFile.checkFile(this.attachList[i].Name).then((data) => {
            console.log(data);
            this.attachList[i].Status = data == true ? '打开' : '下载';
          }).catch((err) => {
            console.log(err);
          });
        }
      }).catch(errText => {
        console.log(errText);
      });
    }

  }

  /**
   * 下载的操作
   * 
   * @param {any} item 
   * @param {any} indx 
   * @memberof LightAttachComponent
   */
  downloadFile(item: any, indx: any) {
    if (this.attachList[indx].Status == "打开") {
      var path = ('file:///storage/emulated/0/1_lightFile/' + item.Name);
      this.lightFileOpen.openFile(path)
        .then((data) => {
          console.log('打开成功');
        })
        .catch(err => {
          this.lightPop.errMessage(err.message);
        })
    } else {
      var url = item.Path;
      this.attachList[indx].Status = "下载中...";
      this.lightFileTransfer.download(url, item.Name)
        .then((data) => {
          const alertSuccess = this.alertCtrl.create({
            title: `下载成功`,
            subTitle: `${item.Name} 下载到以下路径：${data}`,
            buttons: [
              {
                text: '确定',
                role: 'ok',
                handler: () => {
                  console.log("ok clicked");
                }
              },
              {
                text: '打开',
                handler: () => {
                  this.lightFileOpen.openFile('file:///storage/emulated/0/1_lightFile/' + item.Name)
                    .then((data) => {
                      console.log('open', data);
                    })
                    .catch(err => {
                      this.lightPop.errMessage(err.message);
                      console.log(err);
                    })
                }
              }
            ]
          });
          alertSuccess.present();
          this.attachList[indx].Status = "打开";
        })
        .catch(errText => {
          this.attachList[indx].Status = "重新下载";
          console.log("下载失败" + errText);
        });
    }
  }


  /**
  * 判断文件类型，显示对应图片
  * 
  * @param {any} input 
  * @returns {*} 
  * @memberof NewDetailPage
  */
  public fileTypeImage(input: any): any {
    var out = "";
    if (LightStringUtil.contain(input, "jpg") || LightStringUtil.contain(input, "png") || LightStringUtil.contain(input, "gif")) {
      out = "jpg";
    } else if (LightStringUtil.contain(input, "ppt") || LightStringUtil.contain(input, "pptx")) {
      out = "ppt";
    } else if (LightStringUtil.contain(input, "txt")) {
      out = "txt";
    } else if (LightStringUtil.contain(input, "doc") || LightStringUtil.contain(input, "docx") || LightStringUtil.contain(input, "wps")) {
      out = "word";
    } else if (LightStringUtil.contain(input, "xls") || LightStringUtil.contain(input, "xlsx")) {
      out = "xls";
    } else if (LightStringUtil.contain(input, "zip") || LightStringUtil.contain(input, "7z")) {
      out = "zip";
    } else if (LightStringUtil.contain(input, "pdf")) {
      out = "pdf";
    } else {
      out = "unknown";
    }
    out = "assets/img/attach/" + out + "_128.png"
    return out;
  }

}



