import { Injectable } from '@angular/core';
import { LightNation } from '../common/lightNation';
import { File } from '@ionic-native/file';

/**
 * light的本地的对象
 * @export
 * @class LightOpen
 * @extends {LightNation}
 */
@Injectable()
export class LightFile extends LightNation {

    /**
     * 根目录底下的文件夹
     * 用来存储要查找的对外数据路径,加上1为了排序
     * @private
     * @type {string}
     * @memberof LightFile
     */
    private _rootFilePath: string = "1_lightFile";

    /**
     * 数据的路径
     * 
     * @private
     * @type {string}
     * @memberof LightFile
     */
    private _dataPath: string = "light";

    /**
     * 
     * 路径文件信息
     * @private
     * @type {string}
     * @memberof LightFile
     */
    private _dataPathFile: string = "light/file/";

    /**
     * 图片文件信息
     * @private
     * @type {string}
     * @memberof LightFile
     */
    private _dataPathPicture: string = "light/picture/";

    /**
     * 缓存文件信息
     * 
     * @private
     * @type {string}
     * @memberof LightFile
     */
    private _dataPathCache: string = "light/cache/";

    /**
     * npm install --save @ionic-native/file;
;
     * 文件工具操作
     * @param {File} file 
     * @memberof LightFile
     */
    constructor(public file: File) {
        super();
    }

    /**
     *  得到图片的路径信息
     * @returns {Promise<any>} 
     * @memberof LightFile
     */
    getRootFilePath(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("");
        if (!this._isWeb()) {
            result = this._getCurrentPath(this.file.externalRootDirectory, this._rootFilePath);
        }
        return result;
    }

    /**
     * 得到根目录的路径名称
     * @returns {string} 
     * @memberof LightFile
     */
    getRootFilePathName(): string {
        return this._rootFilePath;
    }

    /**
     * 设置图片的路径名称
     * @param {string} value 
     * @memberof LightFile
     */
    setRootFilePathName(value: string): void {
        this._rootFilePath = value;
    }

    ///得到当前的路径信息
    private _getCurrentPath(directory: string, pathName: string): Promise<any> {
        var that = this;
        var result = new Promise(function (resolve, reject) {
            that.file.checkDir(directory, pathName).then(
                pictureResult => {
                    resolve(directory + pathName + "/");
                }
            ).catch(errText => {
                that.file.createDir(directory, pathName, false).then(createResult => {
                    resolve(createResult.toURL());
                }).catch(createErrText => {
                    reject("储存权限未打开，请打开系统设置-应用管理-对应程序-权限，打开");
                });
            });
        });
        return result;
    }

    //得到文件的路径信息
    getFilePath(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("");
        if (!this._isWeb()) {
            result = this._getCurrentPath(this.file.dataDirectory, this._dataPathFile);
        }
        return result;
    }

    //得到图片的路径信息
    getPicturePath(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("");
        if (!this._isWeb()) {
            result = this._getCurrentPath(this.file.dataDirectory, this._dataPathPicture);
        }
        return result;
    }

    /**
     * 得到缓存路径信息
     * 
     * @returns {Promise<any>} 
     * @memberof LightFile
     */
    getCachePath(): Promise<any> {
        var result: Promise<any> = this._getDefaultResult("");
        if (!this._isWeb()) {
            result = this._getCurrentPath(this.file.dataDirectory, this._dataPathCache);
        }
        return result;
    }

    /**
     * 检查是否存在该文件并返回值
     * byjiaxin
     * 
     * @param {string} fileName 
     * @returns {Promise<any>} 
     * @memberof LightFile
     */
    checkFile(fileName: string): Promise<any> {
        var that = this;
        var name = encodeURI(fileName);
        var result = new Promise(function (resolve, reject) {
            that.getRootFilePath()
                .then((path) => {
                    that.file.checkFile(path, name)
                        .then((checkResult) => {
                            resolve(checkResult);
                        })
                        .catch(err => {
                            reject(err);
                        })
                })
                .catch((err) => {
                    reject(err);
                })
        })
        return result;
    }

}


