import { LightStringUtil } from '../../light-util/light-util.module';

/**
 * 本地插件的抽象类
 * @export
 * @abstract
 * @class LightNation
 */
export abstract class LightNation {

    /**
     * 平台的对象信息
     * @memberof LightNation
     */
    constructor() { }

    protected _isWeb(): boolean {
        var result = false;
        var ua = window.navigator.userAgent.toLowerCase();
        if (LightStringUtil.contain(ua, "windows") || LightStringUtil.contain(ua, "sm-g900p") || LightStringUtil.contain(ua, "nexus 5")) {
            result = true;
        }
        return result;
    }

    /**
     * 得到默认的结果信息
     * 
     * @param {Promise<any>} defaultResult 
     * @returns {Promise<any>} 
     * @memberof LightNation
     */
    protected _getDefaultResult(defaultResult: any): Promise<any> {
        var result: Promise<any> = new Promise(function (resolve, reject) {
            resolve(defaultResult);
        });
        return result;
    }
}


