export class LightBrowserUtil {

    /**
     * 判断是否微信访问的浏览器
     * @static
     * @returns {boolean} 
     * @memberof LightBrowserUtil
     */
    public static isWeixinBrowser(): boolean {
        var result: boolean = false;
        var ua = window.navigator.userAgent.toLowerCase();
        var regex: RegExp = /micromessenger/g;
        if (regex.test(ua)) {
            return true;
        } else {
            return false;
        }
    }

}


