///GUID的工具类
export class LightGuidUtil{

     private static chars:Array<any> = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');

     /**
      * 获取UUID2的字符串
      * 
      * @static
      * @param {any} len 
      * @param {any} radix 
      * @returns {string} 
      * @memberof LightGuidUtil
      */
     public static generateUUID2 (len, radix):string {
        let uuid = [], i;
        radix = radix || LightGuidUtil.chars.length;
    
        if (len) {
            // Compact form
            for (i = 0; i < len; i++) uuid[i] = LightGuidUtil.chars[0 | Math.random() * radix];
        } else {
            // rfc4122, version 4 form
            var r;
    
            // rfc4122 requires these characters
            uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
            uuid[14] = '4';
    
            // Fill in random data. At i==19 set the high bits of clock sequence as
            // per rfc4122, sec. 4.1.5
            for (i = 0; i < 36; i++) {
                if (!uuid[i]) {
                    r = 0 | Math.random() * 16;
                    uuid[i] = LightGuidUtil.chars[(i == 19) ? (r & 0x3) | 0x8 : r];
                }
            }
        }
        return uuid.join('');
    }

    /**
     * 获取Guid的信息
     * @static
     * @returns 
     * @memberof LightGuidUtil
     */
    public static generateUUID(){
        return LightGuidUtil.generateUUID2(8,10);
    }
}


