import { CoordinateUtil } from './CoordinateUtil';

export enum WebSpatialReference {
    WGS84 = 1,
    GCJ02 = 2,
    BD_09 = 3
}

export class LatLng {

    public latitude: number;

    public longitude: number;

    public spatialReference: WebSpatialReference = null;

    constructor(latitude: number, longitude: number, spatialreference?: WebSpatialReference) {
        this.latitude = latitude;
        this.longitude = longitude;
        this.spatialReference = spatialreference;
    }

    public toBD_09(): LatLng {
        let result: LatLng = null;

        switch (this.spatialReference) {
            case WebSpatialReference.BD_09:
                break;
            case WebSpatialReference.GCJ02:
                result = CoordinateUtil.transformFromGCJToBD(this);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.BD_09;
                break;
            case WebSpatialReference.WGS84:
                result = CoordinateUtil.transformFromWGSToGCJ(this);
                result = CoordinateUtil.transformFromGCJToBD(result);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.BD_09;
                break;
            default:
                throw new Error("Latlng's 空间参考未定义");
        }
        return this;
    }

    public toGCJ_02(): LatLng {
        let result: LatLng = null;

        switch (this.spatialReference) {
            case WebSpatialReference.BD_09:
                result = CoordinateUtil.transformFromBDToGCJ(this);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.GCJ02;
                break;
            case WebSpatialReference.GCJ02:
                break;
            case WebSpatialReference.WGS84:
                result = CoordinateUtil.transformFromWGSToGCJ(this);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.GCJ02;
                break;
            default:
                throw new Error("Latlng's 空间参考未定义");
        }
        return this;
    }

    public toWGS_84(): LatLng {
        let result: LatLng = null;

        switch (this.spatialReference) {
            case WebSpatialReference.BD_09:
                result = CoordinateUtil.transformFromBDToGCJ(this);
                result = CoordinateUtil.transformFromGCJToWGS(result);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.WGS84;
                break;
            case WebSpatialReference.GCJ02:
                result = CoordinateUtil.transformFromGCJToWGS(this);
                this.latitude = result.latitude;
                this.longitude = result.longitude;
                this.spatialReference = WebSpatialReference.WGS84;
                break;
            case WebSpatialReference.WGS84:
                break;
            default:
                throw new Error("Latlng's 空间参考未定义");
        }
        return this;
    }

}


