import { IFormList } from '../../light-common/index';
/**
 * 写入的的对象信息
 * 
 * @export
 * @class LightWriterAppend
 */
export class LightWriterSimpleAppend {

    /**
     * 标题名称
     * 
     * @private
     * @type {string}
     * @memberof LightWriterSimpleAppend
     */
    private _titleName: string = "";

    /**
     * 表单的列表
     * @private
     * @type {IFormList}
     * @memberof LightWriterSimpleAppend
     */
    private _formList: IFormList = null;

    //group的开始的对象
    private _groupBegin: any = null;

    //group的结束的对象
    private _groupEnd: any = null;

    //数据的列表
    private _dataList: Array<any> = [];

    /**
     * 写入的基本接口信息
     * @param {string} [titleName=null] 
     * @memberof LightWriterSimpleAppend
     */
    constructor(
        titleName: string = ""
    ) {
        this._titleName = titleName;
        this._groupBegin = this._createGroupBeginObject(this._titleName);
        this._groupEnd = this._createGroupEndObject();
    }

    /**
     * 增加标签的对象
     * 
     * @param {string} txt 
     * @param {*} value 
     * @memberof LightWriterSimpleAppend
     */
    public addLabelObject(txt: string, value: any): void {
        this._dataList.push(this._createLabelObject(txt, value));
    }

    /**
     * 增加标签的对象
     * 
     * @param {string} txt 
     * @param {*} value 
     * @memberof LightWriterSimpleAppend
     */
    public addTextObject(txt: string, value: any): void {
        this._dataList.push(this._createTextObject(txt, value));
    }

    /**
     * 转换为数据的列表
     * 
     * @returns {Array<any>} 
     * @memberof LightWriterSimpleAppend
     */
    public toDataList(): Array<any> {
        var result: Array<any> = [];
        result.push(this._groupBegin);
        this._dataList.forEach(element => {
            result.push(element);
        });
        result.push(this._groupEnd);
        return result;
    }

    /**
     * 创建标签对象
     * 
     * @private
     * @param {string} txt 
     * @param {*} value 
     * @returns {*} 
     * @memberof LightWriterSimpleAppend
     */
    private _createLabelObject(txt: string, value: any): any {
        return this._createRowObject(txt, "label", "");
    }

    /**
     * 创建文本对象
     * 
     * @private
     * @param {string} txt 
     * @param {*} value 
     * @returns {*} 
     * @memberof LightWriterSimpleAppend
     */
    private _createTextObject(txt: string, value: any): any {
        return this._createRowObject(txt, "text", "");
    }

    /**
     * 创建Group的开始对象
     * 
     * @private
     * @param {string} txt 
     * @returns {*} 
     * @memberof LightWriterSimpleAppend
     */
    private _createGroupBeginObject(txt: string): any {
        return this._createRowObject(txt, "groupbegin", "");
    }

    /**
     * 创建Group的结束对象
     * 
     * @private
     * @returns {*} 
     * @memberof LightWriterSimpleAppend
     */
    private _createGroupEndObject(): any {
        return this._createRowObject("", "groupend", "");
    }

    /**
     * 创建行的对象信息
     * 
     * @private
     * @param {string} txt 
     * @param {string} type 
     * @param {string} value 
     * @memberof LightWriterSimpleAppend
     */
    private _createRowObject(txt: string, type: string, value: string): any {
        return { txt: txt, type: type, value: value, extend: {} };
    }

}


