import { Injectable } from '@angular/core';
import { LightHttp } from '../../light-providers/light-providers.module';
import { LightJsonUtil } from '../../light-util/light-util.module';
import { LightWriterFlow } from './light-writer.flow';
import 'rxjs/add/operator/toPromise';

/**
 * 写入表格服务操作
 * 
 * @export
 * @class LightWriterService
 */
@Injectable()
export class LightWriterService {

    /**
     * 得到流程表单信息
     * 
     * @private
     * @type {string}
     * @memberof LightWriterService
     */
    private _getEditFormUrl: string = "Flow/GetEditForm";

    /**
     * 得到当前环节链接
     * @private
     * @type {string}
     * @memberof LightWriterService
     */
    private _getCurStepUrl: string = "Flow/GetCurStep";

    /**
     * 得到下一步阶段环节信息
     */
    private _getNextStepUrl: string = "Flow/GetSubNextStep";

    /**
     * 得到下一步阶段用户信息
     * @private
     * @type {string}
     * @memberof LightWriterService
     */
    private _getNextStepUser: string = "Flow/GetNextStepUser";

    /**
     * 上一个环节链接信息
     */
    private _preStepUrl: string = "Flow/GetPreStep";

    /**
     * 归档的流程链接
     * @private
     * @type {string}
     * @memberof LightWriterService
     */
    private _archiveFlowUrl: string = "Flow/ArchiveFlow";

    /**
     * 保存编辑表单链接
     * @private
     * @type {string}
     * @memberof LightWriterService
     */
    private _saveEditFormUrl: string = "Flow/SaveEditForm";

    /**
     * 写入的服务对象信息
     * @memberof LightWriterService
     */
    constructor(public lightHttp: LightHttp) {

    }

    /**
     * 得到编辑的流程信息
     * @param {string} objectID 
     * @param {string} step 
     * @returns 
     * @memberof LightWriterService
     */
    public getWriterFlow(objectID: string): Promise<LightWriterFlow> {
        var result: Promise<LightWriterFlow> = null;
        if (!!objectID) {
            var resultObject: LightWriterFlow = new LightWriterFlow(this.lightHttp);
            resultObject.objectID = objectID;
            var that = this;
            result = new Promise(function (resolve, reject) {
                that.lightHttp.get(that._getCurStepUrl, { objectID: objectID }).then(value => {
                    resultObject.curStepObject = LightJsonUtil.toObject(value);
                    that.lightHttp.get(that._preStepUrl, { objectID: objectID, curStepID: resultObject.getCurStepID() }).then(value => {
                        resultObject.preStepObject = LightJsonUtil.toObject(value);
                        that._getWriterFlowInfo(that, objectID, resultObject, resolve, reject);
                    }).catch(errText => {
                        that._getWriterFlowInfo(that, objectID, resultObject, resolve, reject);
                        //reject(errText);
                    });
                }).catch(errText => {
                    reject(errText);
                });
            });
        }
        return result;
    }

    /**
     * 获取写入流程的信息
     * @param that 
     * @param objectID 
     * @param resultObject 
     * @param resolve 
     * @param reject 
     */
    private _getWriterFlowInfo(that: any, objectID: any, resultObject: LightWriterFlow, resolve: Function, reject: Function): void {
        that.lightHttp.get(that._getNextStepUrl, { objectID: objectID, curStepID: resultObject.getCurStepID() }).then(value => {
            resultObject.nextStepObject = LightJsonUtil.toObject(value);
            if (resultObject.isNextStepSingle()) {
                that.lightHttp.get(that._getNextStepUser, { stepID: resultObject.getNextStepFristID() }).then(value => {
                    resultObject.nextStepUserObject = LightJsonUtil.toObject(value);
                    resolve(resultObject);
                }).catch(errText => {
                    reject(errText);
                });
            } else {
                resolve(resultObject);
            }
        }).catch(errText => {
            reject(errText);
        });
    }

    /**
     * 得到下一步用户的流程信息
     * 
     * @param {LightWriterFlow} writerFlow 
     * @returns {Promise<LightWriterFlow>} 
     * @memberof LightWriterService
     */
    public getNextWriterUserFlow(resultObject: LightWriterFlow, stepID: string): Promise<LightWriterFlow> {
        var that = this;
        var result = new Promise<LightWriterFlow>(function (resolve, reject) {
            that.lightHttp.get(that._getNextStepUser, { stepID: stepID }).then(value => {
                resultObject.nextStepUserObject = LightJsonUtil.toObject(value);
                resolve(resultObject);
            }).catch(errText => {
                reject(errText);
            });
        });
        return result;
    }

    /**
     * 获取写入的表单信息
     * @param {LightWriterFlow} writerFlow 
     * @returns {Promise<LightWriterFlow>} 
     * @memberof LightWriterService
     */
    public getWriterForm(writerFlow: LightWriterFlow): Promise<any> {
        return this.lightHttp.get(this._getEditFormUrl, { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID() });
    }

    /**
     * 保存流程 表单信息
     * @param {LightWriterFlow} writerFlow 
     * @param {*} formContent 
     * @param {boolean} checkMustFields 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public saveWriterForm(writerFlow: LightWriterFlow, formContent: any, checkMustFields: boolean = false): Promise<any> {
        return this.lightHttp.post(this._saveEditFormUrl, { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID(), content: formContent, checkMustFields: checkMustFields });
    }

    /**
     * 提交表单对象
     * @param {LightWriterFlow} writerFlow 
     * @param {*} userList 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public submitForm(writerFlow: LightWriterFlow, userList: Array<any>, nextStepID: string): Promise<any> {
        var userText = "";
        if (!!userList) {
            userText = userList.join(";");
        }
        return this.lightHttp.get("Flow/Submit", { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID(), user: userText, stepID: nextStepID });
    }

    /**
     * 转交流程信息
     * @param {LightWriterFlow} writerFlow 
     * @param {Array<any>} userList 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public transferFlow(writerFlow: LightWriterFlow, user: any, reason: string): Promise<any> {
        return this.lightHttp.get("Flow/TransferFlow", { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID(), user: user, reason: reason });
    }

    /**
     * 流程的归档信息
     * @param {LightWriterFlow} writerFlow 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public archiveFlow(writerFlow: LightWriterFlow): Promise<any> {
        return this.lightHttp.get(this._archiveFlowUrl, { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID() });
    }

    /**
     * 打回流程信息
     * 
     * @param {LightWriterFlow} writerFlow 
     * @param {string} backToStepID 
     * @param {string} submitToStepID 
     * @param {string} reason 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public backToFlow(writerFlow: LightWriterFlow, backToStepID: string, submitToStepID: string, reason: string): Promise<any> {
        return this.lightHttp.get("Flow/BackToFlow", { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID(), backToStepID: backToStepID, submitToStepID: submitToStepID, reason: reason });
    }

    /**
     * 得到流程的日志信息
     * @param {LightWriterFlow} writerFlow 
     * @returns {Promise<any>} 
     * @memberof LightWriterService
     */
    public getFlowLog(writerFlow: LightWriterFlow): Promise<any> {
        return this.lightHttp.get("Flow/GetFlowStepLog", { code: writerFlow.objectID });
    }

}


