import { NgModule } from '@angular/core';

import { HttpModule } from '@angular/http';
import { IonicModule } from 'ionic-angular';
import { LightUtilModule } from '../light-util/light-util.module';

import { LightFile, LightFileTransfer, LightAppVersion, LightCamera, LightFileOpen, LightScan, LightAudio, LightMotion, LightLocationService, LightImagePicker, LightPermissisons, LightMinimize, LightJPush, LightHotCodePush } from './light-nation.index';

import { ZBar } from '@ionic-native/zbar';
import { FileTransfer } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { Camera } from '@ionic-native/camera';
import { ImagePicker } from '@ionic-native/image-picker';
import { Diagnostic } from '@ionic-native/diagnostic';
import { DeviceMotion } from '@ionic-native/device-motion';
import { NativeAudio } from '@ionic-native/native-audio';
import { FileOpener } from '@ionic-native/file-opener';
import { AppVersion } from '@ionic-native/app-version';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { JPush } from '@jiguang-ionic/jpush';
import { HotCodePush } from '@ionic-native/hot-code-push';
import { AppMinimize } from '@ionic-native/app-minimize';

@NgModule({
    imports: [
        HttpModule,
        IonicModule,
        LightUtilModule
    ],
    declarations: [],
    providers: [
        LightFile, LightFileTransfer,
        LightAppVersion, LightCamera, LightFileOpen,
        LightScan, LightAudio, LightMotion,
        LightLocationService, LightPermissisons, AppVersion,
        LightMinimize, LightJPush, LightHotCodePush,
        JPush, AndroidPermissions, ZBar,
        FileTransfer, File, Camera,
        ImagePicker, Diagnostic, DeviceMotion,
        NativeAudio, FileOpener, LightImagePicker,
        HotCodePush, AppMinimize
    ],
    exports: []
})
export class LightNationModule { }
export * from './light-nation.index';



