import { Component, OnInit } from '@angular/core';
import { LightWriterCommon } from '../common/index';

@Component({
    selector: 'light-writer-date',
    templateUrl: 'light-writer-date.component.html',
    // styleUrls: ['light-writer-date.component.scss']
})
export class LightWriterDateComponent extends LightWriterCommon implements OnInit {

    /**
     * 时间类型
     * @type {string}
     * @memberof LightWriterDateComponent
     */
    dateType: string = "";

    /**
     * 时间组件对象
     * @memberof LightWriterDateComponent
     */
    constructor(

    ) {
        super();
    }

    /**
     * 初始化信息
     */
    public ngOnInit() {
        super.ngOnInit();
        if (this.rowType == "dateyymmdd") {
            this.dateType = "date";
        }
        else if (this.rowType == "datetime") {
            this.dateType = "datetime-local";
        }
        else if (this.rowType == "dateyymm") {
            this.dateType = "month";
        }
        else {

        }
    }

}



