import { LoadingController } from 'ionic-angular';
import { Injectable } from '@angular/core';

@Injectable()
export class LightLoading {

    private autoTimeClose: number = 100000;//自动关闭的时间

    private loader: any = null; //加载条

    constructor(
        public loadingCtrl: LoadingController
    ) {

    }

    /**
     * 显示加载条
     * 
     * @param {*} func 显示加载条的回调函数
     * @memberof LightLoading
     */
    public presentLoading(title: string, completeHandler?: Function): Promise<any> {
        this.loader = this.loadingCtrl.create({
            content: title,
            duration: this.autoTimeClose,
        });
        if (!!completeHandler) {
            this.loader.onDidDismiss(() => {
                completeHandler();
            });
        }
        return this.loader.present();
    }

    /**
     * 停止加载条
     * 
     * @param {any}
     * @memberof LightLoading
     */
    public stopLoading(): void {
        this.loader.dismiss();
    }

}


