import { LoadingController } from 'ionic-angular';
import { Injectable } from '@angular/core';
import { ZBar, ZBarOptions } from '@ionic-native/zbar';
import { LightNation } from '../common/lightNation';

@Injectable()
export class LightScan extends LightNation {

    ///zbar参数信息
    private options: ZBarOptions = null;

    /**
     * 加载nation插件命令: npm install --save @ionic-native/zbar
     * 加载ionic插件: ionic cordova plugin add cordova-plugin-cszbar
     * Creates an instance of LightScan.
     * @param {ZBar} zbar 
     * @memberof LightScan
     */
    constructor(private zbar: ZBar) {
        super();
        this.options = { flash: 'off', text_title: '扫码', text_instructions: '请把摄像头对准二维码进行扫描', drawSight: false };
    }

    ///扫描二维码信息
    public scan(resultDefault: any = null): Promise<any> {
        var that = this;
        if (!!!resultDefault) {
            resultDefault = "http://localhost:8225/Projects/SCYK/MyForms/ASSETCOMMON/PrintCard2.aspx?AssetDocUNID=05BEE97B2A354677B0B5C97D926D2359";
        }
        var result: Promise<any> = this._getDefaultResult(resultDefault);
        if (!this._isWeb()) {
            if (!!that.zbar) {
                result = new Promise(function (resolve, reject) {
                    that.zbar.scan(that.options).then(result => {
                        resolve(result);
                    }).catch(errText => {
                        reject(errText);
                    });
                });
            } else {
                alert("zbar插件未安装");
            }
        }
        return result;
    }

}


