import { Injectable } from '@angular/core';
import { MapList } from '../../light-util/light-util.module';

///用户对象的信息
@Injectable()
export class LightMemberImage {

    //颜色列表
    colorArray: Array<string> = ['#3ab9ff', '#30d2e9', '#f4c600', '#13c7a9'];

    //地图映射
    mapList: MapList = new MapList();

    constructor(

    ) {

    }

    //得到随机的颜色信息
    getRandomColor(key: string): string {
        var result: string = null;
        if (!!key) {
            if (!this.mapList.containkey(key)) {
                this.mapList.put(key, this.colorArray[this._getRandomIndex()]);
            }
            result = this.mapList.getValue(key) as string;
        }
        return result;
    }

    //得到随机的索引信息
    private _getRandomIndex(): number {
        return Math.floor(Math.random() * 4);
    }

}


