import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LightProviders, LightApp, LightLocalCache } from '../../../light/light-out/light-out.module';
import { TabsPage } from '../tabs/tabs';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
//登录的页面信息
export class LoginPage extends LightApp {

  //用户名称
  userName: string = "";

  //用户密码
  userpassword: string = "";

  //是否选中
  isRemenber: any = false;

  /**
   * 登录页面的初始化
   * @param {NavController} navCtrl 
   * @param {NavParams} navParams 
   * @param {LightProviders} lightProvider 
   * @memberof LoginPage
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public lightProvider: LightProviders,
    public lightLocalCache: LightLocalCache
  ) {
    super(navCtrl, lightProvider);
    this.isRemenber = this.lightLocalCache.get('isRemenber') ? true : false;
    if (!!this.isRemenber) {
      this.userName = this.lightLocalCache.get('SysUserName');
    } else {
      this.userName = '';
    }
  }

  /**
   * 
   * 登录的操作
   * @memberof LoginPage
   */
  login() {
    this.loginToPage(this.userName, this.userpassword, TabsPage);
  }

  /**
   * 
   * 是否记住账号
   * @memberof LoginPage
   */
  isRemenberFn() {
    if (this.isRemenber) {
      this.lightLocalCache.set('isRemenber', 'true')
    } else {
      this.lightLocalCache.set('isRemenber', '')
    }
  }

}



