import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { UserObject } from '../common/UserObject';
import { OrganizeObject } from '../common/OrganizeObject';
import { LightContactUtil } from '../common/LightContactUtil';
import { LightHttp } from '../../../light-providers/light-providers.module';

@Component({
    selector: 'contact-all',
    templateUrl: 'contact.component.html'
})
export class ContactComponent implements OnInit {

    //搜索的标识信息
    searchplaceholder: string = '搜索';

    searchText: string = "";

    //跟目录的组织机构节点
    private _rootOrganize: OrganizeObject = null;

    //选择的目录的机构信息
    selectOrganize: OrganizeObject = null;

    //上一级的组织列表
    preOrganzieList: Array<OrganizeObject> = [];

    //子一级的组织列表
    childOrganizeList: Array<OrganizeObject> = [];

    //子一级的组织列表
    childUserList: Array<UserObject> = [];

    //contact的工具类
    private _lightContactUtil: LightContactUtil = null;

    //选择事件信息
    @Output() selectEvent = new EventEmitter<UserObject>();

    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public lightHttp: LightHttp
    ) {

    }

    //初始化的信息
    ngOnInit(): void {
        this._lightContactUtil = new LightContactUtil(this.lightHttp);
        this._lightContactUtil.getRootOrganizeObject('User/GetUserList').then(value => {
            this._rootOrganize = value;
            this._setSelectOrganzie(this._rootOrganize);
        });
    }

    /**
     * 设置上一级的组织机构列表
     * 
     * @private
     * @param {OrganizeObject} selectOrganize 
     * 
     * @memberof LightContact
     */
    private _setSelectOrganzie(selectOrganize: OrganizeObject): void {
        this.selectOrganize = selectOrganize;
        this.preOrganzieList = selectOrganize.getPreOrganizeList();
        this.childOrganizeList = selectOrganize.childOrganize;
        this.childUserList = selectOrganize.childUser;
    }

    /**
     * 选择组织机构的事件信息
     * 
     * @param {OrganizeObject} selectOrganize 
     * 
     * @memberof LightContact
     */
    public selectOrganizeEvent(selectOrganize: OrganizeObject): void {
        this._setSelectOrganzie(selectOrganize);
    }


    /**
     * 选择用户的事件信息
     * @param {UserObject} userObject 
     * 
     * @memberof LightContact
     */
    public selectUserEvent(userObject: UserObject): void {
        this.selectEvent.emit(userObject);
    }

    /**
     * 搜索的方法
     * @param {string} 搜索内容 
     * 
     * @memberof LightContact
     */
    public search(content: string): void {
        if (!!this.searchText) {
            this.childOrganizeList = this._rootOrganize.getSearchOrganizeList(this.searchText);
            this.childUserList = this._rootOrganize.getSearchUserList(this.searchText);
            this.preOrganzieList = this._rootOrganize.getPreOrganizeList();
        } else {
            this._setSelectOrganzie(this._rootOrganize);
        }
    }
}



