/**
 * 表单的动作信息
 * @export
 * @interface IFormAction
 */
export class FormFomat {

    ///组件的开始标识
    public static GroupStartType: string = "groupbegin";

    //组件的结束标识
    public static GroupEndType: string = "groupend";

    ///表格的开始标识
    public static TableStartType: string = "tablebegin";

    ///表格的结束标识
    public static TableEndType: string = "tableend";

    ///行的开始标识
    public static RowStartType: string = "rowbegin";

    ///行的结束标识
    public static RowEndType: string = "rowend";

}


