///Light的Json的工具类
export class LightStringUtil {

    /**
        * 字符串格式信息
        * @static
        * @param {string} 字符串内容 
        * @param {string} 参数信息 
        * @returns {string} 
        * 
        * @memberof LightStringUtil
        */
    public static format(content: string, paramTemp: string): string {
        if (!!paramTemp) {
            var reg = /{{\d+}}/gm;
            return content.replace(reg, function (match, name) {
                return paramTemp[name];
            });
        }
        else {
            return content;
        }
    }

    /**
       * 
       * 包含的相关信息
       * @static
       * @param {string} 源字符串信息
       * @param {string} 目标信息 
       * @returns {boolean} 
       * 
       * @memberof LightStringUtil
       */
    public static contain(sourceString: string, targetString: string): boolean {
        var result: boolean = false;
        if (!!sourceString && !!targetString) {
            var index: number = sourceString.indexOf(targetString);
            if (index >= 0) {
                result = true;
            }
        }
        return result;
    }

    /**
     * 判断是否为字符串信息
     * @param source 
     */
    public static isString(source: any): boolean {
        var result: boolean = false;
        if (!!source && typeof source === 'string') {
            result = true;
        }
        return result;
    }

    /**
     * 替代所有的内容信息
     * @static
     * @param {string} 内容 
     * @param {string} 源字符串信息 
     * @param {string} 目标字符串信息 
     * @returns {string} 
     * 
     * @memberof LightStringUtil
     */
    public static replaceAll(content: string, source: string, target: string): string {
        var result = content;
        if (!!source && LightStringUtil.contain(content, source)) {
            result = result.split(source).join(target);
        }
        return result;
    }

    /**
     * 看是否like子字符串
     * @static
     * @param {string} 内容 
     * @param {string} 比对的字符串 
     * @returns {boolean} 
     * 
     * @memberof LightStringUtil
     */
    public static like(content: string, source: string): boolean {
        var result: boolean = false;
        if (!!content && !!source) {
            var index: number = content.search(source);
            if (index >= 0) {
                result = true;
            }
        }
        return result;
    }

}


