import { Component, Input } from '@angular/core';
import { IFormList, FormListAction } from '../../light-common/index';
import { LightWriterConfig } from '../light-writer.config';

@Component({
    //moduleId: module.id,
    selector: 'light-writer-group',
    templateUrl: 'light-writer-group.component.html',
    //styleUrls: ['light-writer-group.component.scss']
})
export class LightWriterGroupComponent {

    //行的列表
    @Input() rowList: IFormList;

    //题目的信息
    title: string;

    //展现的标识
    showFlag: boolean = true;

    //展现的状态信息
    showStatus: string = "";

    //头部的显示标识
    headFlag: boolean = true;

    //读取数据的信息
    @Input() writerData: any = {};

    //写入的配置信息
    @Input() writerConfig: LightWriterConfig = null;

    //内部初始化
    ngOnInit(): void {
        this.title = this.rowList.title;
        var actionList = this.rowList.activeData as FormListAction;
        if (!!actionList) {
            this.headFlag = actionList.headVisable;
            this.showFlag = actionList.displayVisable;
        }
        this.headFlag = this.writerConfig.headFlag;
        this._showStatusInit(this.showFlag);
    }

    //展现的鼠标事件
    showClick(): void {
        if (this.showFlag) {
            this.showFlag = false;
        } else {
            this.showFlag = true;
        }
        this._showStatusInit(this.showFlag);
    }

    //展现状态初始化
    private _showStatusInit(flag: boolean): void {
        if (!flag) {
            this.showStatus = "点击展开";
        } else {
            this.showStatus = "点击折叠";
        }
    }

    showEvent(): void {
        if (this.showFlag) {
            this.showFlag = false;
        } else {
            this.showFlag = true;
        }
    }

}



