import { Injectable } from '@angular/core';


@Injectable()
/**
 * 一些用于优化的方法
 * 
 * @export
 * @class LightMethods
 */
export class LightMethods {

	lastTime: number = 0;

	timer: any = null;

	constructor(

	) {

	}

	/**
	 * 节流，处理多次点击多次弹出页面的情况
	 * 
	 * @param {Function} handler 
	 * @param {number} wait 
	 * @memberof LightMethods
	 */
	doThrottle(handler: Function, wait: number) {
		let nowTime = new Date().getTime();
		if (nowTime - this.lastTime > 1000) {
			console.log('true');
			handler();
			this.lastTime = nowTime;
		}
	}

	/**
	 * 防抖，处理搜索输入触发搜索返回的频率
	 * 
	 * @param {Function} handler 
	 * @param {number} deley 
	 * @memberof LightMethods
	 */
	doDebounce(handler: Function, deley: number) {
		clearTimeout(this.timer);
		this.timer = setTimeout(function () {
			handler();
		}, deley);
	}

}


