import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

//动画库
import { ModalFromBottomLeave, ModalFromBottomEnter } from './modal-transition';
import { Config } from 'ionic-angular';

//基础页面
import { LoginPage, TabsPage } from '../pages/common/index';
import { HomePage } from '../pages/homePage/homePage';
import { PersonalPage } from '../pages/personalPage/personalPage';

//框架引入
import { LightOutModule } from '../light/light-out/light-out.module';

//基础插件
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    TabsPage,
    HomePage,
    PersonalPage
  ],
  imports: [
    BrowserModule,
    LightOutModule,
    IonicModule.forRoot(MyApp, {
      backButtonIcon: 'ios-arrow-back-outline',//按钮图标样式
      backButtonText: '返回',//按钮内容
      tabsHideOnSubPages: 'true',
      iconMode: 'ios'
      // mode: "ios"
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    TabsPage,
    HomePage,
    PersonalPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule {

  constructor(public config: Config) {
    this.setCustomTransitions();
  }

  private setCustomTransitions() {
    this.config.setTransition('modal-from-bottom-enter', ModalFromBottomEnter);
    this.config.setTransition('modal-from-bottom-leave', ModalFromBottomLeave);
  }

}


