import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';

//用户对象的信息
@Injectable()
export class LightPlatform {

    /**
     * 平台的对象信息
     * @param {Platform} platform 
     * 
     * @memberof LightPlatform
     */
    constructor(
        public platform: Platform
    ) {

    }

    /**
     * 判断是否为IOS的系统
     * @returns {boolean} 
     * @memberof LightPlatform
     */
    isIOS(): boolean {
        var result: boolean = false;
        if (this.platform.is('ios')) {
            result = true;
        }
        return result;
    }

    /**
     * 判断是否为Android的系统
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isIPad(): boolean {
        var result: boolean = false;
        if (this.platform.is('ipad')) {
            result = true;
        }
        return result;
    }

    /**
     * 判断是否为Android的系统
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isIPhone(): boolean {
        var result: boolean = false;
        if (this.platform.is('iphone')) {
            result = true;
        }
        return result;
    }

    /**
     * 判断是否为Android的系统
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isAndroid(): boolean {
        var result: boolean = false;
        if (this.platform.is('android')) {
            result = true;
        }
        return result;
    }

    /**
     * 判断是否为Window的浏览器
     * 
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isWindows(): boolean {
        var result: boolean = false;
        //console.log(this.platform.platforms);
        if (this.platform.is('windows')) {
            result = true;
        }
        return result;
    }

    /**
     * 判断是否为web的界面
     * @returns {boolean} 
     * 
     * @memberof LightPlatform
     */
    isMobileWeb(): boolean {
        var result: boolean = false;
        if (this.platform.is('mobileweb')) {
            result = true;
        }
        return result;
    }

}



