import { Injectable } from '@angular/core';
import { LightHttp } from './lightHttp';
import { LightPop } from './lightPop';
import { LightLocalCache } from './lightLocalCache';
import { LightUser } from './lightUser';
import { LightNav } from './lightNav';
import { LightExit } from './lightExit';
import { LightModal } from './lightModal';
import { LightConfig } from './lightConfig';

//用户对象的信息
@Injectable()
export class LightProviders {

    /**
     * 提供器列表
     * @param {LightHttp} lightHttp 
     * @param {LightPop} lightPop 
     * @param {LightLocalCache} lightLocalCache 
     * @memberof LightProviders
     */
    constructor(
        public lightConfig: LightConfig,
        public lightHttp: LightHttp,
        public lightExit: LightExit,
        public lightNav: LightNav,
        public lightModal: LightModal,
        public lightPop: LightPop,
        public lightLocalCache: LightLocalCache,
        public lightUser: LightUser
    ) {

    }

    /**
     * 模板初始化
     * @param {NavController} nav 
     * @memberof LightProviders
     */
    public templateInit(): void {

    }

    /**
     * 页面的跳转
     * @param page 
     * @param data 
     */
    public push(page: any, data: any): void {
        this.lightNav.push(page, data);
    }

    /**
     * 页面的跳转
     * @param page 
     * @param data 
     */
    public back(page: any, data: any): void {
        this.lightNav.back(page, data);
    }

    /**
     * 弹出消息对象
     * @param message 
     * @param title 
     */
    public popMessage(message: string, title: string = null): void {
        this.lightPop.message(message, title);
    }

    /**
     * 设置基础的url消息
     * @param baseUrlTemp 
     */
    public setBaseUrl(baseUrlTemp: string): void {
        this.lightConfig.baseUrl = baseUrlTemp;
    }

}


