import { Component } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';
import { LightContactComponent } from '../../light/light-out/light-out.module';

@Component({
  selector: 'page-home',
  templateUrl: 'homePage.html'
})
export class HomePage {

  constructor(
    public toastCtrl: ToastController,
    public navCtrl: NavController
  ) {

  }

  toLightList() {
    this.navCtrl.push('LightListPage');
  }

  toLightWriter() {
    this.navCtrl.push('LightWriterPage');
  }

  toLightRead() {
    this.navCtrl.push('LightReadPage');
  }

  toLightPopList() {
    this.navCtrl.push('LightPopListPage');
  }

  toLightContent() {
    this.navCtrl.push(LightContactComponent);
  }

  toLightAttach() {
    this.navCtrl.push('LightAttachPage');
  }

  toLightHttp() {
    this.navCtrl.push('LightHttpPage');
  }

}



