/**
 * 
 * 
 * @export
 * @class LightFunctionUtil
 */
export class LightFunctionUtil{

    /**
     * 得到方法的名称
     * @static
     * @returns {string} 
     * @memberof LightFunctionUtil
     */
    public static getFunctionName(target:any):string{
        var result:string="";
        if(LightFunctionUtil.isFunction(target)){
            result=target.name;
        }
        return result;
    }

    /**
     * 判断是否为方法
     * 
     * @static
     * @param {*} target 
     * @returns {boolean} 
     * @memberof LightFunctionUtil
     */
    public static isFunction(target:any):boolean{
        var result:boolean=false;
        if(!!target && typeof target ==='function'){
            result=true;
        }
        return result;
    }

}


