import { Injectable } from '@angular/core';
import { Platform, IonicApp, Events, Keyboard, NavController } from 'ionic-angular';
import { LightUserConfig } from './lightUserConfig';
import { LightHttp } from './lightHttp';
import { LightLocalCache } from './lightLocalCache';
import { LightJsonUtil } from '../../light-util/light-util.module';
import { LightMinimize } from '../../light-nation/light-nation.module';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

//用户对象的信息
@Injectable()
export class LightUser {

    //获取用户登录的Url
    private _userLoginUrl: string = "User/CheckUser";

    //获取用户信息的Url
    private _userInfoUrl: string = "User/GetUserInfo";

    //用户更新密码的Url
    private _userUpdatePasswordUrl: string = "User/ModifyUserPass";

    //系统用户名称的key
    private _userNameKey: string = "SysUserName";

    //系统用户密码的Key
    private _userPasswordKey: string = "SysUserPassword";

    //用户信息的key
    private _userInfoKey: string = "SysUserInfo";

    //用户的默认参数信息
    private _defaultKey: string = "_sysName";

    //用户信息
    private static _userInfo: any = null;

    //用户的配置信息
    constructor(
        private platform: Platform,
        private events: Events,
        private keyboard: Keyboard,
        public lightMinimize: LightMinimize,
        private userConfig: LightUserConfig,
        private lightHttp: LightHttp,
        public localCache: LightLocalCache
    ) {

    }


    /**
     * 用户的登录信息
     * 
     * @param {string} 用户名称 
     * @param {string} 用户密码 
     * @returns {Promise<any>} 
     * @memberof LightUser
     */
    userLogin(userName: string, userPassword: string): Promise<any> {
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            if (!!userName) {
                that.lightHttp.post(that._userLoginUrl, { userAcount: userName, userPass: userPassword })
                    .then(function () {
                        that._userLogin(userName, userPassword).then(function (data) {
                            resolve(data);
                        }).catch(function (errText: string) {
                            reject(errText);
                        })
                    }).catch(function (errText: string) {
                        reject(errText);
                    })
            } else {
                reject();
            }
        });
        return result;
    }

    /**
     * 自动登录的操作
     * 
     * 
     * @memberof LightUserService
     */
    autoLoginCheck(): Promise<any> {
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            if (that.localCache.contain(that._userNameKey)
                && that.localCache.contain(that._userPasswordKey)) {
                that.userLogin(that.localCache.get(that._userNameKey),
                    that.localCache.get(that._userPasswordKey)).then(function () {
                        resolve();
                    }).catch(errText => {
                        reject(errText);
                    });
            } else {
                reject("用户缓存不存在");
            }
        });
        return result;
    }

    /**
     * 更新账户信息
     * 
     * @param {any} newPassword 
     * @param {any} oldPassword 
     * 
     * @memberof LightUserService
     */
    updatePassword(newPassword, oldPassword): Promise<any> {
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            if (that.userConfig.equalLoginPassword(oldPassword)) {
                that.lightHttp.post(that._userUpdatePasswordUrl, {
                    userAcount: that.userConfig.getLoginName(),
                    userPass: oldPassword,
                    userNewPass: newPassword
                }).then
                    (function () {
                        that._userCacheInit(that.userConfig.getLoginName(), newPassword);
                        resolve();
                    }).catch(function () {
                        reject();
                    });
            }
        });
        return result;
    }

    /**
     * 用户退出
     * @memberof LightUserService
     */
    userExit(): void {
        // this.localCache.remove(this._userNameKey);
        this.localCache.remove(this._userPasswordKey);
        this.userConfig.clear();
        LightUser._userInfo = null;
        //this._userInfo=null;
    }

    /**
     * 得到用户的名称
     * @returns {string} 
     * 
     * @memberof LightUserService
     */
    getUserName(): string {
        var result: string = "";
        var userInfo = this.getUserInfo();
        if (!!userInfo) {
            result = userInfo['UserName'];
        }
        return result;
    }

    /**
     * 获取用户的基本信息
     * @returns {*} 
     * @memberof LightUser
     */
    getUserInfo(): any {
        var result: any;
        if (!!LightUser._userInfo) {
            result = LightUser._userInfo;
        } else {
            if (this.localCache.contain(this._userInfoKey)) {
                result = LightUser._userInfo = LightJsonUtil.toObject(this.localCache.get(this._userInfoKey));
            }
        }
        return result;
    }

    /**
     * 用户登录的信息
     * 
     * @private
     * @param {string} 用户名称 
     * @param {string} 用户密码 
     * @returns {Promise<any>} 
     * 
     * @memberof LightUserService
     */
    private _userLogin(userName: string, userPassword: string): Promise<any> {
        var that = this;
        this._userCacheInit(userName, userPassword);
        var result: Promise<any> = new Promise(function (resolve, reject) {
            that.lightHttp.post(that._userInfoUrl, { userAcount: userName }).then(function (data) {
                LightUser._userInfo = LightJsonUtil.toObject(data);
                that.lightHttp.setDefaultParam(that._defaultKey, LightUser._userInfo["UserName"]);
                that.localCache.set(that._userInfoKey, data);
                resolve(data);
            }).catch(function (errText: string) {
                reject(errText);
            })
        });
        return result;
    }

    /**
     * 用户缓存初始化
     * @private
     * @param {string} 用户名称 
     * @param {string} 用户密码 
     * 
     * @memberof LightUserService
     */
    private _userCacheInit(userName: string, userPassword: string): void {
        this.userConfig.login(userName, userPassword);
        this.localCache.set(this._userNameKey, userName);
        this.localCache.set(this._userPasswordKey, userPassword);
    }

    /**
     * 安卓物理返回按键处理
     */
    registerBackButtonAction(ionicApp: IonicApp, navCtrl: NavController) {
        if (this.platform.is('ios')) {
            return;
        }
        this.platform.registerBackButtonAction(() => {
            this.events.publish('android:backButtonAction');
            if (this.keyboard.isOpen()) { // 如果键盘开启则隐藏键盘
                this.keyboard.close();
                return;
            }
            // 如果想点击返回按钮隐藏toast或loading或Overlay就把下面加上
            // this.ionicApp._toastPortal.getActive() ||this.ionicApp._loadingPortal.getActive()|| this.ionicApp._overlayPortal.getActive()
            const activePortal = ionicApp._modalPortal.getActive() || ionicApp._toastPortal.getActive() || ionicApp._overlayPortal.getActive();
            if (activePortal) {
                activePortal.dismiss();
                return;
            }
            const childNav = navCtrl.getActiveChildNavs()[0]; // 获取tabs导航,this.nav是总导航,tabs是子导航
            if (!childNav) {
                this.lightMinimize.minimize();
                return;
            }
            const tab = childNav.getSelected(); // 获取选中的tab
            const activeVC = tab.getActive(); // 通过当前选中的tab获取ViewController
            const activeNav = activeVC.getNav(); // 通过当前视图的ViewController获取的NavController
            return activeNav.canGoBack() ? activeNav.pop() : this.lightMinimize.minimize();
        }, 1);
    }

}


