import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LightProviders, LightApp } from './../../light/light-out/light-out.module';
import { LoginPage } from './../common/login/login'

@Component({
  selector: 'page-personal',
  templateUrl: 'personalPage.html'
})
export class PersonalPage extends LightApp {

  constructor(
    public navCtrl: NavController,
    public lightProviders: LightProviders
  ) {
    super(navCtrl, lightProviders);
  }

  showMessage() {
    this.navCtrl.push('LightMessageComponent');
  }

  //二维码分享
  showShare() {
    var baseUrl = this.lightProviders.lightHttp.getUrl("MobileHtml/WriterDimensionalImage");
    var contentData = "<div><img src='" + baseUrl + "'></div>";
    let alert = this.lightProviders.lightPop.alertCtrl.create({
      //title: '二维码分享',
      message: contentData,
      buttons: [
        {
          text: '关闭',
          role: 'cancel',
          handler: () => {
            //console.log('不进行更新');
          }
        }
      ]
    });
    alert.present();
  }


  //意见反馈
  showAdvisory() {
    this.navCtrl.push('LightFeedbackComponent');
  }

  //个人信息
  showPersonInfo() {
    this.navCtrl.push('LightPersonComponent');
  }

  //退出登录
  logout() {
    this.logoutPage(LoginPage);
  }

}



