import { Component } from '@angular/core';
import { ViewController } from 'ionic-angular';
import { LightProviders } from '../../../light-providers/light-providers.module';
import { UserObject } from '../../light-contact/index';

@Component({
    //moduleId: module.id,
    selector: 'handle-single-contact',
    templateUrl: 'handle-single-contact.component.html',
    //styleUrls: ['handle-single-contact.component.scss']
})
export class HandleSingleContactComponent {

    //用户列表
    userList: Array<any> = [];

    /**
     * 联系人单选的组件操作
     * @param {LightProviders} lightProvider 
     * @param {ViewController} viewCtrl 
     * @memberof HandleSingleContactComponent
     */
    constructor(
        public lightProvider: LightProviders,
        public viewCtrl: ViewController
    ) {

    }

    /**
     * 回退的操作
     * @memberof HandleMutiContactComponent
     */
    goBack(): void {
        this.lightProvider.lightModal.dismiss(this.viewCtrl);
    }

    /**
     * 通讯录选择
     * @param {UserObject} userObject 
     * @memberof HandleMutiContactComponent
     */
    contactSelect(userObject: UserObject): void {
        this.userList.push(userObject.Name);
        this.lightProvider.lightModal.dismiss(this.viewCtrl, this.userList);
    }

}



