import { Injectable } from '@angular/core';
import { DeviceMotion, DeviceMotionAccelerometerOptions, DeviceMotionAccelerationData } from '@ionic-native/device-motion';
import { LightNation } from '../common/lightNation';
import { Subscription } from 'rxjs/Subscription';

@Injectable()
export class LightMotion extends LightNation {

    ///motion参数信息
    private options: DeviceMotionAccelerometerOptions = null;

    private _MotionSubscription: Subscription;//控制摇一摇的变量

    /**
     * @memberof LightMotion
     */
    constructor(
        private deviceMotion: DeviceMotion
    ) {
        super();
        this.options = { frequency: 1000 };
    }

    //开启摇一摇监听
    public watchAcceleration(handler: (data: DeviceMotionAccelerationData) => void): any {
        this.clearAcceleration();

        if (!this._isWeb()) {
            return this._MotionSubscription = this.deviceMotion.watchAcceleration(this.options).subscribe((acceleration: DeviceMotionAccelerationData) => handler(acceleration));
        } else {
            return;
        }
    }

    //关闭监听
    public clearAcceleration(): void {
        !!this._MotionSubscription && this._MotionSubscription.unsubscribe();
    }
}


