import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { LightProviders } from '../../../light-providers/light-providers.module';
import { HandleSingleContactComponent } from '../handle-single-contact/handle-single-contact.component';
import { LightFlowService } from '../light-flow.service';
import { LightWriterFlow } from '../../light-writer/index';

@Component({
    //moduleId: module.id,
    selector: 'light-flow-deliver',
    templateUrl: 'light-flow-deliver.component.html',
    //styleUrls: ['light-flow-deliver.component.scss']
})
export class LightFlowDeliverComponent {

    //原因信息
    reason: string = "";

    //转交人信息
    deliverPeople: string = "";

    //写入流程信息
    writerFlow: LightWriterFlow = null;

    /**
     * 流程转交的组件对象
     * @param {NavController} navCtrl 
     * @param {NavParams} navParam 
     * @memberof LightFlowDeliverComponent
     */
    constructor(
        public navCtrl: NavController,
        public viewCtrl: ViewController,
        public navParam: NavParams,
        public lightProvider: LightProviders,
        public flowService: LightFlowService
    ) {
        this.writerFlow = navParam.data.flow as LightWriterFlow;
    }

    /**
     *  
     * @memberof LightFlowDeliverComponent
     */
    showContactSingleModal() {
        this.lightProvider.lightModal.push2(HandleSingleContactComponent, value => {
            if (!!value && value.length > 0) {
                this.deliverPeople = value[0];
            }
        });
    }

    /**
     * 提交任务的详细
     */
    submitTaskDetail(): void {
        if (!!this.deliverPeople) {
            //this.lightProvider.lightModal.dismiss(this.viewCtrl);
            this.flowService.transferFlow(this.navCtrl, this.writerFlow, this.deliverPeople, this.reason);
        } else {
            this.lightProvider.popMessage("转交人不能为空");
        }
    }

}



