import { Injectable } from '@angular/core';
import { NativeAudio } from '@ionic-native/native-audio';
import { LightNation } from '../common/lightNation';

@Injectable()
export class LightAudio extends LightNation {

    private musicList: any[] = null;

    public get MusicList(): string[] {
        return this.musicList;
    }

    /**
     * @memberof LightMotion
     */
    constructor(
        private nativeAudio: NativeAudio
    ) {
        super();
        this.musicList = [];
    }

    //预加载音乐
    public preloadSimple(id: string, path: string): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            if (!this._isWeb()) {
                this.nativeAudio.preloadSimple(id, path)
                    .then(() => {
                        this.musicList.push(id);
                        resolve(id);
                    })
                    .catch(err => {
                        reject(err);
                    });
            } else {
                reject("非手机环境");
            }
        });
    }

    //从内存中卸载音乐
    public unloadAudios(id: string): void {
        let index = this.musicList.indexOf(id);
        if (index != -1) {
            this.nativeAudio.unload(id);
            this.musicList.splice(this.musicList[index], 1);
        }
    }

    //播放音乐
    public playAudio(id: string, handler?: Function): void {
        let index = this.musicList.indexOf(id);
        if (index != -1) {
            this.nativeAudio.play(id, handler);
        }
    }

    //暂停音乐
    public stopAudio(id: string): void {
        let index = this.musicList.indexOf(id);
        if (index != -1) {
            this.nativeAudio.stop(id);
        }
    }

}


