import { Injectable } from '@angular/core';
import { LightNation } from '../common/lightNation';
import { FileOpener } from '@ionic-native/file-opener';

/**
 * light的本地的对象
 * @export
 * @class LightOpen
 * @extends {LightNation}
 */
@Injectable()
export class LightFileOpen extends LightNation {

    /**
     * 本地的安装： npm install --save @ionic-native/file-opener
     * 插件的安装:  ionic cordova plugin add cordova-plugin-file-opener2
     * 本地打开对象信息
     * @memberof LightOpen
     */
    constructor(private fileOpener: FileOpener) {
        super();
    }

    /**
     * 打开文件对象
     * 
     * @param {string} path 
     * @memberof LightOpen
     */
    openFile(path: string, defaultResult: any = null): Promise<any> {
        var that = this;
        if (!!!defaultResult) {

        }
        var result: Promise<any> = this._getDefaultResult(defaultResult);
        if (!!path) {
            if (!this._isWeb()) {
                result = new Promise(function (resolve, reject) {
                    var ext = that._getFileExt(path)
                    var type = that._getFileType(ext);
                    that.fileOpener.open(path, type).then(result => {
                        resolve(result);
                    }).catch(errText => {
                        reject(errText);
                    });
                });
            }
        }
        return result;
    }

    /**
     * 得到文件的扩展名称
     * @private
     * @param {string} str 
     * @returns {string} 
     * @memberof LightOpen
     */
    private _getFileExt(str: string): string {
        var d = /\.[^\.]+$/.exec(str);
        return d.toString();
    }

    /**
     * 得到文件类型
     * @private
     * @param {string} path 
     * @returns {string} 
     * @memberof LightOpen
     */
    private _getFileType(ext: string): string {
        var result = "";
        if (!!ext) {
            switch (ext) {
                case '.txt':
                    result = 'text/plain';
                    break;
                case '.jpg':
                case '.png':
                case '.gif':
                case '.bmp':
                case '.jpeg':
                    result = 'image/*';
                    break;
                case '.pdf':
                    result = 'application/pdf';
                    break;
                case '.doc':
                case '.docx':
                    result = 'application/msword';
                    break;
                case '.xls':
                case '.xlsx':
                    result = 'application/vnd.ms-excel';
                    break;
                case '.ppt':
                case '.pptx':
                    result = 'application/vnd.ms-powerpoint';
                    break;
                case '.apk':
                    result = 'application/vnd.android.package-archive';
                    break;
                case '.dwg':
                    result = 'application/x-autocad';
                    break;
                default:
                    result = 'application/octet-stream';
            }
        }
        return result;
    }

}


